* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", serif;
}
body {
  background-color: var(--color-background);
}
span {
  color: var(--color-touch);
  font-weight: 600;
  text-align: center;
 
  padding-top: 10px;
}

h1 {
  text-align: center;
  font-family: "Poppins", serif;
}

h4 {
  font-family: "Poppins", serif;
}

h3 {
  text-align: center;
}

:root {
  --color-background: #ffffff;
  --color-background-2: #f5f5f5;
  --color-touch: #a368a8;
  --color-cont: #f8f9fa;
  --color-text: aliceblue;
  --color-text2: rgb(37, 35, 35);
}

.light-mode {
  --color-background: black;
  --color-touch: wheat;
  --color-cont: #222222;
  --color-text: black;
  --color-text2: #a9a9a9;
}

.main-logo {
  width: 200px;

  margin: 10px;
  margin-left: 70px;
}

.container {
  width: 1200px;

  border-radius: 30px;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
}

.container-2 {
  width: 1200px;

  border-radius: 30px;
  margin-left: auto;
  margin-right: auto;
}

#About {
  background-color: var(--color-background-2);
  padding: 30px 0;
}

.pos-cont {
  width: 700px;
  height: 400px;
  border-radius: 30px;
}

.about-us {
  font-size: 50px;
  font-weight: 300;
  color: var(--color-text2);
  text-align: left;
}

.about-col-1 {
  display: flex;
  flex-direction: column;
}

.about-us-sub {
  font-size: 35px;
  font-weight: 200;
  color: var(--color-text2);
  text-align: left;
  margin-bottom: 1rem;
  line-height: 1.3;
}

.about-col {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  align-items: center;
}

.about-details {
  font-size: 16px;
  color: var(--color-text2);
  font-weight: 300;
  text-align: left;
}

.btn1 {
  background: var(--color-touch);
  padding: 8px 30px;
  font-size: 18px;
  width: 200px;
  border-radius: 30px;
  cursor: pointer;
  color: #fff;
  transition: 0.8s;
}

.btn1:hover {
  border: 2px solid var(--color-touch);
  background-color: #fff;
  color: var(--color-touch);
}

.btn2 {
  background: var(--color-touch);
  margin-left: 8px;
  border: 2px solid var(--color-text);
  border-radius: 10px;
  cursor: pointer;
  color: var(--color-text);
  transition: 0.8s;
  overflow: hidden;
  position: relative;
  padding: 8px 100px;
}

.btn2:hover {
  background-color: var(--color-text);
  color: var(--color-touch);
}

.pos-image,
.pos-image2,
.pos-img {
  width: 100%;
 
}

.how-to-text{
  padding-right: 30px;
}

.pos-details,
.pos-detail {
  font-size: 20px;
  color: var(--color-text2);
  font-weight: 300;
  text-align: justify;
}

.hero-img {
  width: 600px;
}

.pos-detail {
  text-align: center;
  width: 80%;
  margin: 0 auto;
}
.pos-head {
  color: var(--color-text2);
  text-align: center;
}

.features {
  background-color: var(--color-touch);
  margin: 10px;
  padding: 0.5px;
}

.features-intro {
  font-size: 20px;
  color: rgb(37, 35, 35);
  text-align: center;
}

.features-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 250px;
  width: 320px;
  margin: 0;
  padding: 0;
}

.features-details > div {
  display: flex;
  justify-content: center;
  height: 300px;
  width: 300px;
  overflow: hidden;
}

.features-row {
  display: flex;
  justify-content: space-around;
  padding-top: 20px;
}

.features-row-2{
  width: 200px !important;
  height: auto !important;
}

.feature-text h3{
font-size: x-large;
color: var(--color-touch);
font-family: poppins;
font-weight: 300;
}

.mobile-img{
  width: 120px !important; 
  height: 120px;
}

.mobile-img-2{
  width: 120px; 
  height: 120px;
  
}

.features-image {
  width: 120px;
  height: auto;
  margin-top: 10px;
}

.pricing {
  width: 320px;
  border-radius: 15px;
  margin: 20px;

  background-color: var(--color-background-2);
}

.price-head {
  color: var(--color-touch);
}

.price {
  background-color: var(--color-touch);
}

.price-details {
  color: var(--color-text2);
  margin: 0;
  
}

.pricing-row {
  display: block;
  
  padding: 1rem 1rem;
}

.fa-arrow-right {
  color: var(--color-touch);
}
.fa-times {
  color: #ff5252;
}
.fa-check {
  color: #4caf50;
  font-size: 20px;
}
.fa-adjust {
  font-size: 30px;
  color: var(--color-touch);
}

.price-features {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;

}

/* .price-features > div {
  display: flex;
  align-items: center;

} */

.price-item , .price-item-icon, .transact{
  display: flex;
  align-items: center;
  padding: 10px 0;
}

.transact span {
  color: var(--color-text2);

}

.fa-arrow-right{
  padding: 6px;
}

.theme-btn {
  top: 11%;
  right: 3%;
  width: 55px;
  height: 55px;
  border-radius: 50px;
  position: fixed;
  background-color: var(--color-cont);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.3);
  transition: all 0.4s ease-in-out;
}
.footer {
  background-color: var(--color-background-2);
}
.footer-head,
.footer-details,
.copyright {
  color: var(--color-text2);
}

.footer-row {
  display: grid;
  grid-template-columns: auto auto auto;
  padding-left: 200px;
  padding-right: 200px;
}
.foot {
  width: 100%;
  background-color: var(--color-text);
}
.copy {
  margin-left: 500px;
}

/*----------Page Scroll Effect----------------*/
.reveal {
  position: relative;
  opacity: 0;
}
.reveal.active {
  opacity: 1;
}
.active.fade-bottom {
  animation: fade-bottom 1s ease-in;
}
.active.fade-bottom1 {
  animation: fade-bottom1 1s ease-in;
}
.active.fade-bottom2 {
  animation: fade-bottom2 1s ease-in;
}
.active.fade-left {
  animation: fade-left 1s ease-in;
}
.active.fade-right {
  animation: fade-right 1s ease-in;
}
@keyframes fade-bottom {
  0% {
    transform: translateY(60px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fade-bottom1 {
  0% {
    transform: translateY(70px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fade-bottom2 {
  0% {
    transform: translateY(90px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fade-left {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fade-right {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/*----------bootstrap-navbar-css------------*/

.navbar-mainbg {
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  height: 70px;

  background-color: var(--color-touch);
}
.navbar-logo {
  padding: 0px;
  color: var(--color-text);
}
.navbar-logo:hover {
  color: var(--color-text);
}

#navbarSupportedContent {
  position: relative;
  padding: 0;
  height: 90px;
}
#navbarSupportedContent ul {
  padding: 0px;
  margin: 0px;
}
#navbarSupportedContent ul li a i {
  margin-right: 10px;
}
#navbarSupportedContent li {
  list-style-type: none;
  float: left;
}
#navbarSupportedContent ul li a {
  color: var(--color-text);
  text-decoration: none;
  font-size: 16px;
  display: block;
  padding: 20px 20px;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  position: relative;
}
#navbarSupportedContent > ul > li.active > a {
  color: var(--color-touch);
  background-color: transparent;
  transition: all 0.7s;
}
#navbarSupportedContent a:not(:only-child):after {
  content: "\f105";
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 14px;
  font-family: "Font Awesome 5 Free";
  display: inline-block;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
  transition: 0.5s;
}
#navbarSupportedContent .active > a:not(:only-child):after {
  transform: rotate(90deg);
}
.hori-selector {
  display: inline-block;
  position: absolute;
  height: 100%;
  top: 0px;
  left: 0px;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  background-color: var(--color-text);
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  margin-top: 10px;
}
.hori-selector .right,
.hori-selector .left {
  position: absolute;
  width: 25px;
  height: 25px;
  background-color: var(--color-text);
  bottom: 15px;
}
.hori-selector .right {
  right: -25px;
}
.hori-selector .left {
  left: -25px;
}
.hori-selector .right:before,
.hori-selector .left:before {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--color-touch);
}
.hori-selector .right:before {
  bottom: 0;
  right: -25px;
}
.hori-selector .left:before {
  bottom: 0;
  left: -25px;
}

@media (max-width: 1024px) {
  .pos-image {
    margin-top: 30%;
  }
  .container {
    margin-left: 20px;
  }
  .features-row {
    padding-left: 0 !important;
    width: 140% !important;
    padding-right: 0 !important;
  }
  .features-details,
  .pricing {
    width: 21% !important;
  }
  .features-image {
    width: 100%;
  }
  .copy {
    margin-left: 0px;
    margin-left: 300px;
  }
}
@media (min-width: 992px) {
  /* .main-logo {
    width: 100%;
    margin-top: 0;
  } */
  .navbar-expand-custom {
    -ms-flex-flow: row nowrap;
    flex-flow: row nowrap;
    -ms-flex-pack: start;
    justify-content: flex-start;
  }
  .navbar-expand-custom .navbar-nav {
    -ms-flex-direction: row;
    flex-direction: row;
  }
  .navbar-expand-custom .navbar-toggler {
    display: none;
  }
  .navbar-expand-custom .navbar-collapse {
    display: -ms-flexbox !important;
    display: flex !important;
    -ms-flex-preferred-size: auto;
    flex-basis: auto;
  }
}
@media (max-width: 991px) {
  #navbarSupportedContent ul li a {
    padding: 12px 30px;
  }
  .hori-selector {
    margin-top: 0px;
    margin-left: 10px;
    border-radius: 0;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
  }
  .hori-selector .left,
  .hori-selector .right {
    right: 10px;
  }
  .hori-selector .left {
    top: -25px;
    left: auto;
  }
  .hori-selector .right {
    bottom: -25px;
  }
  .hori-selector .left:before {
    left: -25px;
    top: -25px;
  }
  .hori-selector .right:before {
    bottom: -25px;
    left: -25px;
  }
}
@media (max-width: 900px) {
